* {
  margin: 0;
  padding: 0;
}
nav {
  height: 5vh;
  width: 100%;
  background-color: #008c9e;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}
nav ul {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  list-style: none;
}
nav ul a {
  text-decoration: none;
}
nav ul a li {
  color: white;
  margin-right: 15px;
  font-weight: bold;
  cursor: pointer;
}
.pageContent {
  height: 95vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: black;
}
.pageContent .homePage h1 {
  color: white;
}
.pageContent .useEffectPage {
  color: white;
}
.pageContent .aboutPage {
  color: white;
  text-align: center;
}
.pageContent .membersPage {
  color: white;
  text-align: center;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 90%;
}
.pageContent .membersPage hr {
  width: 90%;
}
.pageContent .membersPage table {
  background-color: white;
  color: black;
  width: 85%;
  height: 75%;
  text-align: center;
  line-height: 1.5em;
}
.pageContent .membersPage table thead {
  background-color: #008c9e;
  color: white;
}
.pageContent .membersPage table tbody tr td:nth-child(1) {
  background-color: #008c9e;
}
.pageContent .membersPage table tbody tr td:nth-child(1) a {
  text-decoration: none;
  font-weight: bold;
  color: white;
}
.pageContent .profilePage {
  height: 100%;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.pageContent .profilePage h1,
.pageContent .profilePage h2,
.pageContent .profilePage h4 {
  margin-bottom: 15px;
}
.pageContent .bookListPage {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.pageContent .bookListPage h1 {
  margin-bottom: 50px;
}
.pageContent .bookListPage .book {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.pageContent .bookListPage .book h2 {
  margin-top: 25px;
}
.pageContent .addBookForm {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
  width: 50%;
  height: 25%;
  background-color: #008c9e;
  border-radius: 15px;
  position: relative;
  bottom: 100px;
}
.pageContent .addBookForm legend {
  color: white;
  font-weight: bold;
  font-size: 1.4em;
}
.pageContent .addBookForm input {
  font-size: 1.3em;
  border-radius: 5px;
  text-align: center;
  background-color: black;
  color: white;
}
.pageContent .addBookForm input::placeholder {
  color: white;
}
.pageContent .addBookForm button {
  background-color: black;
  color: white;
  width: 40%;
  font-size: 1.1em;
  padding: 5px;
}
